module.exports = "import { Component, OnInit } from '@angular/core';\nimport {\n  FormBuilder,\n  FormControl,\n  FormGroup\n} from '@angular/forms';\n\n@Component({\n  selector: 'nz-demo-form-advanced-search',\n  template: `\n    <form nz-form [formGroup]=\"validateForm\" class=\"ant-advanced-search-form\">\n      <div nz-row [nzGutter]=\"24\">\n        <div nz-col [nzSpan]=\"8\" *ngFor=\"let control of controlArray\" [style.display]=\"control.show?'block':'none'\">\n          <nz-form-item nzFlex>\n            <nz-form-label [nzFor]=\"'field'+control.index\">Field {{control.index}}</nz-form-label>\n            <nz-form-control>\n              <input nz-input placeholder=\"placeholder\" [formControlName]=\"'field'+control.index\" [attr.id]=\"'field'+control.index\">\n            </nz-form-control>\n          </nz-form-item>\n        </div>\n      </div>\n      <div nz-row>\n        <div nz-col [nzSpan]=\"24\" style=\"text-align: right;\">\n          <button nz-button [nzType]=\"'primary'\">Search</button>\n          <button nz-button (click)=\"resetForm()\">Clear</button>\n          <a style=\"margin-left:8px;font-size:12px;\" (click)=\"toggleCollapse()\">\n            Collapse\n            <i nz-icon [type]=\"isCollapse?'down':'up'\"></i>\n          </a>\n        </div>\n      </div>\n    </form>\n    <div class=\"search-result-list\">\n      Search Result List\n    </div>\n  `,\n\n  styles: [\n      `\n      .ant-advanced-search-form {\n        padding: 24px;\n        background: #fbfbfb;\n        border: 1px solid #d9d9d9;\n        border-radius: 6px;\n      }\n\n      .search-result-list {\n        margin-top: 16px;\n        border: 1px dashed #e9e9e9;\n        border-radius: 6px;\n        background-color: #fafafa;\n        min-height: 200px;\n        text-align: center;\n        padding-top: 80px;\n      }\n\n      [nz-form-label] {\n        overflow: visible;\n      }\n\n      button {\n        margin-left: 8px;\n      }\n    `\n  ]\n})\nexport class NzDemoFormAdvancedSearchComponent implements OnInit {\n  validateForm: FormGroup;\n  controlArray = [];\n  isCollapse = true;\n\n  toggleCollapse(): void {\n    this.isCollapse = !this.isCollapse;\n    this.controlArray.forEach((c, index) => {\n      c.show = this.isCollapse ? (index < 6) : true;\n    });\n  }\n\n  resetForm(): void {\n    this.validateForm.reset();\n  }\n\n  constructor(private fb: FormBuilder) {\n  }\n\n  ngOnInit(): void {\n    this.validateForm = this.fb.group({});\n    for (let i = 0; i < 10; i++) {\n      this.controlArray.push({ index: i, show: i < 6 });\n      this.validateForm.addControl(`field${i}`, new FormControl());\n    }\n  }\n}\n"