module.exports = "import { Component, OnInit } from '@angular/core';\nimport {\n  FormBuilder,\n  FormGroup,\n  Validators\n} from '@angular/forms';\n\n@Component({\n  selector: 'nz-demo-form-horizontal-login',\n  template: `\n    <form nz-form [nzLayout]=\"'inline'\" [formGroup]=\"validateForm\" (ngSubmit)=\"submitForm()\">\n      <nz-form-item>\n        <nz-form-control>\n          <nz-input-group [nzPrefix]=\"prefixUser\">\n            <input formControlName=\"userName\" nz-input placeholder=\"Username\">\n          </nz-input-group>\n          <nz-form-explain *ngIf=\"validateForm.get('userName').dirty && validateForm.get('userName').errors\">Please input your username!</nz-form-explain>\n        </nz-form-control>\n      </nz-form-item>\n      <nz-form-item>\n        <nz-form-control>\n          <nz-input-group [nzPrefix]=\"prefixLock\">\n            <input formControlName=\"password\" nz-input type=\"password\" placeholder=\"Password\">\n          </nz-input-group>\n          <nz-form-explain *ngIf=\"validateForm.get('password').dirty && validateForm.get('password').errors\">Please input your Password!</nz-form-explain>\n        </nz-form-control>\n      </nz-form-item>\n      <nz-form-item>\n        <nz-form-control>\n          <button nz-button nzType=\"primary\" [disabled]=\"!validateForm.valid\">Log in</button>\n        </nz-form-control>\n      </nz-form-item>\n    </form>\n    <ng-template #prefixUser><i nz-icon type=\"user\"></i></ng-template>\n    <ng-template #prefixLock><i nz-icon type=\"lock\"></i></ng-template>\n`\n})\nexport class NzDemoFormHorizontalLoginComponent implements OnInit {\n  validateForm: FormGroup;\n\n  submitForm(): void {\n    for (const i in this.validateForm.controls) {\n      this.validateForm.controls[ i ].markAsDirty();\n      this.validateForm.controls[ i ].updateValueAndValidity();\n    }\n  }\n\n  constructor(private fb: FormBuilder) {\n  }\n\n  ngOnInit(): void {\n    this.validateForm = this.fb.group({\n      userName: [ null, [ Validators.required ] ],\n      password: [ null, [ Validators.required ] ],\n      remember: [ true ]\n    });\n  }\n}\n"